import React from "react";
import styled from "styled-components";
import { device } from "../data/device";

import { sections } from "../data/sections";

const HeaderWrapper = styled.header`
  width: 100%;
  height: 100px;
  display: block;
  background-color: ${props => props.theme.colors.background};
`;

const LogoWrapper = styled.div`
  float: left;
`;

const Logo = styled.h1`
  margin: 30px 0 0 40px;
  color: ${props => props.theme.colors.textColor};
  &::after {
    content: "u";
  }
`;

const NavigationWrapper = styled.nav`
  width: 500px;
  float: right;
  margin: 35px 35px 0 0;
  display: none;

  @media ${device.tablet} {
    display: block;
  }

  @media ${device.laptop} {
    width: 650px;
  }
`;

const Link = styled.a`
  text-decoration: none;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.8rem;
  padding: 10px;
  margin: 0 30px;
  color: ${props => props.theme.colors.textColor};

  transition: 0.2s opacity;
  opacity: 0.5;
  &:hover {
    opacity: 1;
  }
`;

const ThemeSwitchButton = styled.button`
  border: none;
  background: ${props => props.theme.colors.buttonColor};
  padding: 5px 15px;
  margin: 0 30px;
  border-radius: 20px;
  cursor: pointer;
  display: none;

  @media ${device.laptop} {
    display: inline-block;
  }
`;

const Header = (props: any) => {
  return (
    <HeaderWrapper>
      <LogoWrapper>
        <Logo>Kacper</Logo>
      </LogoWrapper>
      <NavigationWrapper>
        {sections.map(({ link, name }: any) => (
          <Link key={link} href={link}>
            {name}
          </Link>
        ))}
        <ThemeSwitchButton onClick={props.isDarkModeFunc}>
          Mode <span role="img" aria-label="Moon">ðŸŒ’</span>
        </ThemeSwitchButton>
      </NavigationWrapper>
    </HeaderWrapper>
  );
};

export default Header;
