import React from "react";
import styled from "styled-components";

import { technologies } from "../data/technologies";
import { device } from "../data/device";

import Card from "../components/Card";

const Container = styled.main`
  background-color: ${props => props.theme.colors.background};
`;

interface WrapperSection {
  bgColor: any;
  theme: any;
}

const WrapperSection: any = styled.div`
  padding: 80px 0 30px 0;
  background-color: ${(props: WrapperSection) =>
    props.bgColor || props.theme.colors.cardBackground};
`;

const TitleSection = styled.h1`
  color: ${props => props.theme.colors.textColor};
  text-align: center;
  margin: 0 0 80px 0;
  font-size: 40px;
  display: block;
`;

const Section = styled.section`
  width: 100%;
  margin: auto;
  display: block;

  @media ${device.laptop} {
    max-width: 1000px;
  }
`;

const Main = () => {
  return (
    <Container>
      <WrapperSection>
        <TitleSection>Technologie</TitleSection>
        <Section>
          {technologies.map((data: any, index: number) => (
            <Card key={data.title} data={data} index={index}></Card>
          ))}
        </Section>
      </WrapperSection>
      {/*  */}
      <WrapperSection bgColor="#F76D6D">
        <TitleSection>Projekty</TitleSection>
        <Section></Section>
      </WrapperSection>
      {/*  */}
      <WrapperSection>
        <TitleSection>O mnie</TitleSection>
        <Section>Section</Section>
      </WrapperSection>
    </Container>
  );
};

export default Main;
